import Vue from 'vue'
import Vuex from 'vuex'
import firebase from 'firebase/app'
import 'firebase/auth'
import db from '../firebase/firebaseInit.js'

Vue.use(Vuex)

export default new Vuex.Store({
	state:{
		// 假資料
		// sampleBlogCards: [
		// 	{blogTitle: 'Blog Card #1', blogCoverPhoto: 'stock-1', blogDate: 'Jun 1, 2022'},
		// 	{blogTitle: 'Blog Card #2', blogCoverPhoto: 'stock-2', blogDate: 'Jun 1, 2022'},
		// 	{blogTitle: 'Blog Card #3', blogCoverPhoto: 'stock-3', blogDate: 'Jun 1, 2022'},
		// 	{blogTitle: 'Blog Card #4', blogCoverPhoto: 'stock-4', blogDate: 'Jun 1, 2022'}
		// ],
		blogPosts: [],
		postLoaded: null,
		blogHTML: "在此寫下文章內容...",
		blogTitle: "",
		blogPhotoName: "",
		blogPhotoFileURL: null,
		blogPhotoPreview: null,
		editPost: null,
		user: null,
		profileEmail: null,
		profileFirstName: null,
		profileLastName: null,
		profileUserName: null,
		profileId: null,
		profileInitials: null,
		webmasterlogin: null,
	},
	getters: {
		blogPostsFeed(state){
			return state.blogPosts.slice(0, 2);
		},
		blogPostsCards(state){
			return state.blogPosts.slice(2, 6);
		}
	},
	mutations: {
		newBlogPost(state, payload){
			state.blogHTML = payload;
			// console.log(state.blogHTML)
		},
		updateBlogTitle(state, payload){
			state.blogTitle = payload;
		},
		fileNameChange(state, payload){
			state.blogPhotoName = payload
		},
		createFileURL(state, payload){
			state.blogPhotoFileURL = payload;
		},
		openPhotoPreview(state){
			state.blogPhotoPreview = !state.blogPhotoPreview;
		},
		toggleEditPost(state, payload){
			state.editPost = payload;
			// console.log(state.editPost);
		},
		updateUser(state, payload){
			state.user = payload
		},
		webmasterlogin(state){
			state.webmasterlogin = true;
		},
		// 將firebase的使用者資料寫入
		setProfileInfo(state, payload){
			state.profileId = payload.id;
			state.profileEmail = payload.data().email;
			state.profileFirstName = payload.data().firstName;
			state.profileLastName = payload.data().lastName;
			state.profileUserName = payload.data().username;
			console.log(state)
			console.log(payload)
		},
		setProfileInitials(state){
			state.profileInitials = 
				state.profileFirstName.match(/(\b\S)?/g).join('') + 
				state.profileLastName.match(/(\b\S)?/g).join('');
		},
		changeFirstName(state, payload){
			state.profileFirstName = payload;
		},
		changeLastName(state, payload){
			state.profileLastName = payload;
		},
		changeUsername(state, payload){
			state.profileUserName = payload;
		},
		filterBlogPost(state, payload){
			state.blogPosts = state.blogPosts.filter(post => post.blogID !== payload);
		},
		setBlogState(state, payload){
			state.blogTitle = payload.blogTitle;
			state.blogHTML = payload.blogHTML;
			state.blogPhotoFileURL = payload.blogCoverPhoto;
			state.blogPhotoName = payload.blogCoverPhotoName;
		}
	},
	actions: {
		async getCurrentUser({commit}){
			const dateBase = await db.collection('users').doc(firebase.auth().currentUser.uid);
			// 獲取firebase資料庫中的使用者資料
			const dbResults = await dateBase.get();
			commit('setProfileInfo', dbResults);
			commit('setProfileInitials');
			console.log(dbResults.id)
			// const token = await user.getIdTokenResult();
			// const admin = await token.claims.admin;
			commit('webmasterlogin')
		},
		// Profile頁變更使用者名稱/姓/名
		async updateUserSettings({commit, state}){
			const dateBase = await db.collection('users').doc(state.profileId);
			await dateBase.update({
				firstName: state.profileFirstName,
				lastName: state.profileLastName,
				username: state.profileUserName,
			});
			commit('setProfileInitials');
		},
		async getPost({state}){
			const dateBase = await db.collection('blogPosts').orderBy('date', 'desc');
			const dbResults = await dateBase.get();
			console.log(dateBase.firestore)
			console.log(dbResults)
			dbResults.forEach((doc) => {
				if(!state.blogPosts.some( (post) => post.blogID === doc.id)){
					const data = {
						blogID: doc.data().blogID,
						blogHTML: doc.data().blogHTML,
						blogCoverPhoto: doc.data().blogCoverPhoto,
						blogTitle: doc.data().blogTitle,
						blogDate: doc.data().date,
						blogCoverPhotoName: doc.data().blogCoverPhotoName,
					};
					state.blogPosts.push(data);
				}
			});
			state.postLoaded = true;
			console.log(state.blogPosts)
		},
		async deletePost({commit}, payload){
			const getPost = await db.collection("blogPosts").doc(payload);
			await getPost.delete();
			commit("filterBlogPost", payload)
		},
		async updateBlog({commit, dispatch}, payload){
			console.log(commit);
			console.log(dispatch);
			console.log(payload);
			commit("filterBlogPost", payload);
			await dispatch("getPost");
		}
	},
	modules: {
	}
})
